<template>
	<div class="misty-input">
		<div class="misty-input-label">
			<label :for="textId">{{title}}</label>
		</div>
		<div class="misty-input-form">
			<input :type="nativeType" ref="input" :id="textId" :style="{'text-align':align}" :placeholder="placeholder" @input="valueChange"
			 v-model="thisValue" @focus="valueFocus" @blur="valueBlur" :maxlength="max" />
			<i class="misty-input-icon" @click="vulueClear" v-show="iconWarnClear"></i>
			<i class="misty-input-warn" v-show="verification"></i>
		</div>
	</div>
</template>

<script type="text/javascript" lang="js">
	import {
		idReg18,
		telReg
	} from './../../until/reg'
	import {
		RandomCharacter,
		getAge
	} from './../../until/common'
	export default {
		name: 'rm-input',
		model: {
			prop: 'value',
			event: 'change'
		},
		props: {
			placeholder: [String, Number],
			title: String,
			type: {
				type: String,
				default: 'text',
				validator: function(value) {
					let valueType = ['text', 'number', 'tel', 'id'].indexOf(value) !== -1
					if (valueType) {
						return valueType
					} else {
						console.warn("The value of isType should be'tel''email''id'.")
					}
				}
			},
			'align': String,
			value: null,
			regexp: RegExp,
			getAge: Function,
			isReturn: {
				type: Object,
				default: () => ({
					type: false,
					massage: '错误信息'
				})
			},
			warn: {
				type: Boolean,
				default: false
			},
			max: Number,
			maxage: {
				type: Number,
				default: 60
			},
			minage: {
				type: Number,
				default: 18
			}
		},
		data() {
			return {
				thisValue: '',
				verification: false,
				iconWarnClear: false,
				textId: '',
				nativeType: ''
			}
		},
		watch: {
			value(nvl) {
				this.thisValue = nvl
			}
		},
		created() {
			const nvl = this.type
			switch (nvl) {
				case 'id':
					this.nativeType = 'text'
					break
				case 'tel':
					this.nativeType = 'tel'
					break
				case 'number':
					this.nativeType = 'tel'
					break
				default:
					this.nativeType = 'text'
					break
			}
			this.textId = 'pa-' + RandomCharacter()
			this.thisValue = this.value
			this.verificationChange(this.value)
		},
		methods: {
			valueChange() {
				const val = event.target.value
				if (this.type === 'tel') {
					const le = 11
					if (val.length > le) {
						this.thisValue = val.substring(0, le)
						return
					}
				}
				if (this.type === 'id') {
					const le = 18
					if (val.length > le) {
						this.thisValue = val.substring(0, le)
						return
					}
				}
				const max = this.max
				if (max) {
					if (val.length > max) {
						this.thisValue = val.substring(0, max)
						return
					}
				}
				this.$emit('change', val)
				this.verificationChange(val)
				this.clearIcon(val)
			},
			vulueClear() {
				this.$emit('change', '')
				this.$emit('clear', '')
				this.thisValue = ''
				this.verificationChange('')
				this.clearIcon('')
				this.$refs.input.focus()
			},
			valueFocus() {
				this.clearIcon(this.thisValue)
			},
			valueBlur() {
				setTimeout(() => {
					this.clearIcon('')
				}, 310)
			},
			clearIcon(value) {
				if (value !== '') {
					this.iconWarnClear = true
				} else {
					this.iconWarnClear = false
				}
			},
			verificationChange(value) {
				const isType = this.type
				if (isType === 'tel') {
					const telreg = this.regexp || telReg
					if (telreg.test(value)) {
						this.verification = false
						this.isReturn.type = true
						this.isReturn.message = 'Y'
					} else {
						if (this.warn) {
							this.verification = true
						}
						this.isReturn.type = false
						this.isReturn.message = '请输入正确的手机号码'
					}
					return
				}
				if (isType === 'id') {
					const fun = (value) => {
						const telreg = this.regexp
						if (telreg) {
							return telreg.test(value)
						} else {
							return (idReg18.test(value) || idReg18.test(value))
						}
					}
					if (fun(value)) {
						const age = this.getAge || getAge
						if (age(value) < this.minage || age(value) > this.maxage) {
							if (this.warn) {
								this.verification = true
							}
							this.isReturn.type = false
							this.isReturn.message = '合法年龄在' + this.minage + '--' + this.maxage + '之间'
							return
						}
						this.verification = false
						this.isReturn.type = true
						this.isReturn.message = 'Y'
					} else {
						if (this.warn) {
							this.verification = true
						}
						this.isReturn.type = false
						this.isReturn.message = '请输入正确的身份证号码'
					}
				}
			}
		}
	}
</script>

<style>
	@import url("../../styles/css/reset.css");

	.misty-input {
		text-align: left;
		display: flex;
		padding: 7px;
		font-size: 14px;
		background: #fff;
		color: inherit;
		border-top: .5px solid #e5e5e5;
		border-bottom: .5px solid #e5e5e5;
	}

	.misty-input-label label {
		display: block;
		width: auto;
		line-height: 24px;
		min-height: 24px;
	}

	.misty-input-form {
		flex: 1;
		display: flex;
		align-items: center;
	}

	.misty-input-form input {
		display: block;
		flex: 1;
		height: 24px;
		font-size: inherit;
		color: inherit;
	}

	.misty-input-icon {
		display: block;
		height: 16px;
		width: 16px;
		border-radius: 50%;
		background: #fbfbfb url(data:image/png;base64,) no-repeat center;
		background-size: 100%;
		margin: 0 5px;
	}

	.misty-input-warn {
		display: block;
		height: 16px;
		width: 16px;
		background: transparent url(data:image/png;base64,) no-repeat center;
		background-size: 130%;
	}
</style>
