<template>
	<div style="display: table;">
		<transition name="MistyToastFade">
			<div class="misty-toast" v-show="show" v-stop>
				<div class="misty-toast-all">
					<template v-if="type === 'success' || type === 'cancel' || type === 'text'">
						<div>
							<i :class="ipStyle" v-if="!(type === 'text')"></i>
							<p :class="ipStyle">
								<slot>{{ text }}</slot>
							</p>
						</div>
					</template>
				</div>
			</div>
		</transition>
	</div>
</template>

<script>
export default {
	name: 'rmToast',
	directives: {
		stop: {
			bind(el) {
				el.addEventListener('touchmove', event => {
					event.preventDefault()
				})
			}
		}
	},
	watch: {
		show(newv, oldv) {
			if (newv) {
				this.trigger()
			}
		},
		value(nvl) {
			this.show = nvl
		}
	},
	computed: {
		ipStyle() {
			return {
				'misty-toast-success': this.type === 'success',
				'misty-toast-cancel': this.type === 'cancel',
				'misty-toast-text': this.type === 'text'
			}
		}
	},
	props: {
		value: {
			type: Boolean,
			default: false
		},
		type: {
			type: String,
			validator(value) {
				return ['success', 'cancel', 'text'].indexOf(value) !== -1 || !value
			},
			default: 'success'
		},
		time: {
			type: Number,
			default: 2000
		},
		text: {
			type: String
		}
	},
	data() {
		return {
			timeout: null,
			show: this.value
		}
	},
	methods: {
		trigger() {
			this.$emit('input', true)
			this.$emit('on-show')
			clearTimeout(this.timeout)
			this.timeout = setTimeout(() => {
				this.$emit('input', false)
				this.show = false
				this.$emit('on-hide')
			}, this.time)
		}
	}
}
</script>

<style>
@import url('../../styles/css/reset.css');
.MistyToastFade-enter-active,
.MistyToastFade-leave-active {
	transition: opacity 0.5s;
}
.MistyToastFade-enter,
.MistyToastFade-leave-to {
	opacity: 0;
}
.misty-toast {
	position: fixed;
	left: 0;
	top: 0;
	z-index: 5000;
	width: 100%;
	height: 100%;
	font-size: 16px;
	display: table;
}
.misty-toast .misty-toast-all {
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	right: 0;
	margin: auto;
	width: 125px;
	max-width: 70%;
	padding-left: 10px;
	padding-right: 10px;
	background: hsla(0, 0%, 7%, 0.7);
	border-radius: 5px;
	display: table;
}
.misty-toast .misty-toast-all > div {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 5px 0;
}
.misty-toast .misty-toast-all i {
	display: block;
	width: 28px;
	height: 28px;
}
.misty-toast .misty-toast-all i.misty-toast-success {
	background: transparent
		url(data:image/png;base64,)
		no-repeat center;
	background-size: 100%;
}
.misty-toast .misty-toast-all i.misty-toast-cancel {
	background: transparent url(../../icons/cancel.png) no-repeat center;
	background-size: 100%;
}
.misty-toast .misty-toast-all i.misty-toast-text {
	display: none;
}
.misty-toast .misty-toast-all p {
	flex: 1;
	line-height: 24px;
	text-align: center;
	color: #fff;
}
.misty-toast .misty-toast-all p.misty-toast-success {
	color: #fff;
}
.misty-toast .misty-toast-all p.misty-toast-cancel {
	color: #fff;
}
</style>
